.social-contact {
    display: flex;
    justify-content: center;
    align-items: center;
}
.social-icon-div {
    /* padding: 6px;
    cursor: pointer; */
    /* margin: 6px; */
    font-family: "SocialShapes"; 
    font-size: 1.4vw;
}


@media only screen and (orientation:portrait) and (max-width: 1024px) {
    .social-icon-div {
        font-size: 2vh;
    }
}

@media only screen and (orientation:landscape) and (max-width: 1024px) {
    .social-icon-div {
        font-size: 4vh;
    }
}